body {
  background-color: $white;
  font-family: $gothic;
  font-size: 16px;
  min-height: 100vh;
  position: relative;
}
.text-ask{
  color: #3a3a3a;
}
.btn-rounded{
  border-radius: 50px !important;
}
.mb-30{
  margin-bottom: 30px !important;
}
.mb-35{
  margin-bottom: 35px !important;
}
.btn-outline-primary{
  &:hover, &:focus, &:active{
    color: #fff;
  }
}
.custom-select-reset{
  border-radius: 2px;
  border: solid 1px $primary;
  height: calc(2.25rem + 9px);
  background: #fff url(data:image/svg+xml;base64,) no-repeat right 0 center;
  &:focus{
    border: solid 1px $primary;
  }
}
.modal-custom{
  .modal-content{
    border-radius: 2px;
    background-color: #ffffff;
    border: solid 10px $primary;
  }
}
.modal-backdrop.show {
  opacity: 0.3;
}
.modal-close-btn{
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 32px;
  color: $primary;
  opacity: 1;
  z-index: 99;
  &:hover, &:focus, &:active{
    color: darken($primary, 10%);
    outline: none;
    box-shadow: none;
  }
  span{
    &:hover, &:focus, &:active{
      color: darken($primary, 10%);
      outline: none;
      box-shadow: none;
    }
  }
}
.btn-reset{
  line-height: 1.9;
}


